es.</p></aside><h2 data-v-38d8e15a>Preguntas o Comentarios</h2><aside data-v-38d8e15a><p data-v-38d8e15a>En caso tenga consultas o comentarios sobre esta Política de Privacidad,puede hacerlo llegar a través del correo contactos@easybill.pe, Gerente General MAKEASY S.R.L.Huánuco, Perú</p></aside></div></div>"),_c('footer-section')],2)}
